const webhost = 'http://helenhunter-001-site1.dtempurl.com/www/';
const wechathost = 'https://api.weixin.qq.com/';
const appid = 'wxb56a85d47db03697';
const app_secret = '395dd24ad1e10c8f54f643a416736b79';
const cVersion = '1.03';
const appName = 'id414478124';
var model = '';
var platform = '';
var version = '';
var manufacturer = '';
var uuid = '';

import Vue from 'vue'
import axios from 'axios';

export default {
  apihost,
  webhost,
  filehost,
  wechathost,
  appid,
  app_secret,
  cVersion,
  appName,
  model,
  platform,
  version,
  manufacturer,
  uuid,
  logerror: function(message) {      
    var storage = window.localStorage;   
    var userid = storage.getItem("userid")  
    var error = "info:" + new Date() + " " + message   
    axios.post(this.apihost + 'api/ExceptionLogs/', {
    Model:this.model,
    Platform:this.platform,
    Version:this.version,
    Manufacturer:this.manufacturer,
    Uuid:this.uuid,
    Message: error,
    Userid: userid   
    })
    .then(response => {      
       
    })
    .catch(e => {   
   
    })
    }  
}








