<template>
  <div>
    <div v-if="userid != null">
    <mt-header fixed>       
        <mt-button slot="left" class="item-content open-panel" v-on:click="usersetting()">
            <img style="width:40px;height:40px;border-radius:50%;" :src="headimgurl" />  
        </mt-button>  
       
        <mt-button slot="right" style="width:200px;margin-right:10px" v-on:click="search()"> 
          <div class="mint-searchbar-inner">
            <i class="mintui mintui-search"> 川菜 | 油条 | 保险 | 快递 ...</i>
            <input class="mint-searchbar-core" />
          </div>
        </mt-button>   

    </mt-header>
    </div>

    <div v-else>
    <mt-header   fixed>       
        <mt-button slot="left" class="item-content open-panel" v-on:click="login()">
          <!--<span class="icon icon-login"></span>--> 
         登陆
        </mt-button>
        <mt-button slot="right" style="width:200px;margin-right:10px"  v-on:click="search()"> 
          <div class="mint-searchbar-inner">
            <i class="mintui mintui-search"> 川菜 | 油条 | 保险 | 快递 ...</i>
            <input class="mint-searchbar-core" />
          </div>
        </mt-button>   
    </mt-header>
    </div>
  </div>
</template>

<script>
  import axios from 'axios';
  import VueRouter from 'vue-router';
  import config from '../Config.js'

 export default {
    data() {
      return {
        headimgurl:'',
        userid: ''
      }
    },
    methods: {
     login()
      {
        this.$router.push({ path: 'login' }); 
      },
      search()
      {
         this.$router.push({ path: 'Search' }); 
      },
      usersetting()
      {
        this.$router.push({ path: 'myinfo' }); 
      },
      setHeader()
      {   
        this.host = config.apihost        
        var storage = window.localStorage;   
        this.userid =  storage.getItem("userid")   
      
        if (this.userid != null)
        {    
            var path = storage.getItem("imagepath")               
            if (path.startsWith("http://wx"))
            {
              this.headimgurl = path 
            }else
            {
              this.headimgurl = this.host + path   
            }          
        }
      }
    },
    activated:function () {
        this.setHeader()
    },
    created() {       
        this.setHeader()       
    }            
 }
 </script>



